/* General Page Styling */
.cart-page {
    background-color: #121212;
    color: #ffffff;
    min-height: 100vh;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .cart-title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
  }
  
  /* Cart Item Styling */
  .cart-item {
    display: flex;
    background-color: #1c1c1c;
    border-radius: 12px;
    margin-bottom: 16px;
    padding: 12px;
  }
  
  .item-image {
    flex: 0 0 100px;
    height: 100px;
    overflow: hidden;
    border-radius: 8px;
  }
  
  .item-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .item-details {
    flex: 1;
    margin-left: 12px;
  }
  
  .item-name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 4px;
  }
  
  .item-description {
    font-size: 14px;
    color: #aaaaaa;
    margin-bottom: 8px;
  }
  
  /* Size Options */
  .size-options {
    display: flex;
    gap: 8px;
    margin-bottom: 8px;
  }
  
  .size-option {
    background-color: #2c2c2c;
    border-radius: 8px;
    padding: 4px 8px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #ffffff;
  }
  
  .size-label {
    font-weight: bold;
    margin-right: 4px;
  }
  
  /* Quantity and Price */
  .quantity-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .quantity-controls {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .quantity-controls button {
    background-color: #e76f51;
    color: #ffffff;
    border: none;
    border-radius: 50%;
    font-size: 16px;
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
  
  .item-price {
    font-size: 18px;
    font-weight: bold;
  }
  
  /* Footer */
  .cart-footer {
    margin-top: auto;
    text-align: center;
  }
  
  .total-price {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 12px;
  }
  
  .pay-button {
    background-color: #e76f51;
    color: #ffffff;
    font-size: 18px;
    font-weight: bold;
    padding: 12px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
    transition: background-color 0.3s ease;
  }
  
  .pay-button:hover {
    background-color: #d55a3b;
  }
  
  .pay-button:active {
    background-color: #c0492c;
  }
  
.remove-button {
  padding: 5px 10px;
  border: none;
  background-color: #ff4d4d;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.remove-button:hover {
  background-color: #d11a2a;
}

.cart-total {
  margin-top: 20px;
  text-align: right;
}

.cart-total h2 {
  font-size: 24px;
  font-weight: bold;
  text-align: left;
}
.order-button{
  width: 100%;
  background-color: #e76f51; /* Orange button */
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  padding: 12px 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
 

}
.toggle-btn{
  background-color: #e76f51;
  color : white;
}

.back-button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 16px; /* Adjust for perfect alignment */
  top: 16px; /* Align with top spacing */
  background: #1a1a1a; /* Dark button background */
  border: none;
  border-radius: 12px; /* Smooth rounded corners */
  padding: 10px;
  width: 40px; /* Square size */
  height: 40px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.back-button:hover {
  background: #333; /* Slight hover effect */
}

.back-button svg {
  width: 20px;
  height: 20px;
  color: white;
}
.order-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-content {
  background: #1a1a1a;
  padding: 20px;
  border-radius: 10px;
  width: 90%;
  max-width: 400px;
  text-align: center;
}

.delivery-form {
  margin-top: 15px;
  
}

.delivery-form label {
  display: block;
  margin-bottom: 10px;
}

.map-button {
  background-color: #007bff;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-top: 10px;
}

.popup-buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.cancel-button {
  background-color: red;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

.confirm-button {
  background-color: green;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}
.formbox{
  color: white;
  background-color: #1e1e1e;
  border: 3px solid #ccc;
  border-radius: 17px;
}

/* Add to cart.css */
.phone-section input[type="tel"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.hint-text {
  font-size: 0.8rem;
  color: #666;
  margin-top: 4px;
}



/* Map Popup */
.map-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.map-popup-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow: auto;
}

.map-container {
  margin: 15px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
}

.map-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

/* Location details section */
.location-details-section {
  margin: 15px 0;
  width: 100%;
}

.location-label-input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  margin-bottom: 8px;
}

.location-hint {
  font-size: 14px;
  color: #666;
  margin-top: 5px;
}

/* Map buttons */
.map-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 15px;
}

.cancel-button, .confirm-button {
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  flex: 1;
  margin: 0 5px;
}

.cancel-button {
  background: #f0f0f0;
  color: #333;
  border: 1px solid #ddd;
}

.confirm-button {
  background: #4CAF50;
  color: white;
  border: none;
}

.confirm-button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.loading-container {
  text-align: center;
  padding: 20px;
}

.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 10px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.location-info {
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
}

.address-textarea,
.notes-textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-family: inherit;
  resize: vertical;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.cancel-button,
.confirm-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.cancel-button {
  background-color: #e0e0e0;
}

.confirm-button {
  background-color: #4caf50;
  color: white;
}

.confirm-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}